@import 'ScssHelpers/helpers-all';
// ------------------- step 4

.step_form__head {
	max-width: 94rem;

	&:not(:last-child) {
		margin-bottom: 3.6rem;
	}

	&.step_form__head--centered_mod {
		text-align: center;
		margin: 0 auto 3.6rem;
	}
}

.step_form__title {
	@include h3;

	letter-spacing: -0.018em;

	&:not(:last-child) {
		margin-bottom: 0.8rem;
	}
}

.step_form__text {
	@include text;

	line-height: 1.5;
}
