@import "ScssHelpers/helpers-all";

.members_slider {
	width: 100%;
	background-color: $beige_1;

	.swiper-wrapper {
		align-items: stretch;
		align-content: stretch;
	}
}

.members_slider__slide {
	&.swiper-slide-active {
		pointer-events: initial;
	}

	@include bp($point_4, min) {
		display: flex !important;
		flex-shrink: 0;
		height: unset !important;
		pointer-events: none;

		@include flex_block(row, nowrap, flex-start, stretch, stretch);
	}

	@include bp($point_4 - 1) {
		@include flex_block(column, nowrap, flex-start, center);
	}
}

.members_slider__media {
	position: relative;

	@include bp($point_4, min) {
		flex-shrink: 0;
		width: clamp(1px, 44.2%, 48rem);
		min-height: 43.2rem;
	}

	@include bp($point_4 - 1) {
		width: 100%;
		height: 0;
		padding-bottom: 60%;
	}

	.memberships_tabs & {
		@include bp($point_2, min) {
			width: 41.6rem;
			min-height: 37.5rem;
		}
		@include bp($point_3) {
			//padding-bottom: 68%;
		}
	}
}

.members_slider__video_trigger {
	position: absolute;
	left: 50%;
	top: 50%;

	color: $white;

	@include bp($point_4, min) {
		width: 12rem;
		height: 12rem;
		margin: -6rem 0 0 -6rem;
	}

	@include bp($point_4 - 1) {
		width: 8rem;
		height: 8rem;
		margin: -4rem 0 0 -4rem;
	}
}

.members_slider__video_wrap,
.members_slider__img_wrap {
	@include bp($point_4, min) {
		width: 100%;
		height: 100%;
		display: block;
	}

	@include bp($point_4 - 1) {
		@include stretch;
	}
}

.members_slider__img {
	@include cover_img;
}

.members_slider__content {
	@include flex_block(column, nowrap, flex-start);

	.memberships_tabs & {
		@include bp($point_2, min) {
			padding: 4.6rem 6.6% 3.2rem 5.7%;
		}
		@include bp($point_3) {
			padding: 2.4rem 2.4rem 3.2rem;
		}
	}

	@include bp($point_4, min) {
		padding: 6.3rem 4.5% 6.5rem 5.7%;
	}

	@include bp($point_4 - 1) {
		padding: 2.6rem 2.4rem 2.8rem;
	}
}

.members_slider__subtitle {
	@include h5;

	margin-bottom: 0.2rem;

	text-transform: uppercase;
	color: $gold_2;
}

.members_slider__title {
	@include h3;

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 1.4rem;
		}
	}

	@include bp($point_4 - 1) {
		letter-spacing: -0.03em;

		&:not(:last-child) {
			margin-bottom: 0.6rem;
		}
	}
}

.members_slider__descr {
	@include text;

	@include bp($point_4, min) {
		line-height: 1.47;
		letter-spacing: 0.002em;

		&:not(:last-child) {
			margin-bottom: 3.8rem;
		}
	}

	@include bp($point_4 - 1) {
		line-height: 1.44;
		letter-spacing: -0.016em;
		color: rgba(0, 0, 0, 0.8);

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.members_slider__controls {
	margin-top: auto;
}

.members_slider__controls_btn {
	&:not(:first-child) {
		margin-left: 0.8rem;
	}
}

.members_slider__pagination {
	@include bp($point_4, min) {
		&:not(:first-child) {
			margin-left: 2.4rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:first-child) {
			margin-left: 1.4rem;
		}
	}
}
