@import "ScssHelpers/helpers-all.scss";

.pairs_list {
}

.pairs_list__title {
	@include h3;

	@include bp($point_4, min) {
		letter-spacing: -0.016em;
		&:not(:last-child) {
			margin-bottom: 2.8rem;
		}
	}

	@include bp($point_4 - 1) {
		letter-spacing: -0.028em;
		line-height: 1.08;

		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}
}

.pairs_list__content {
	background-color: $gray_3;

	@include bp($point_4, min) {
		padding: 4.8rem 5rem 4.8rem 6.4rem;
	}

	@include bp($point_4 - 1) {
		padding: 2.4rem 2.4rem 2.8rem;
	}
}

.pairs_list__items {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, stretch, flex-start);

		margin: 0 -1rem -1.6rem 0;
	}
	@include bp($point_3) {
	}
}

.pairs_list__item {
	min-height: 100%;

	@include bp($point_2, min) {
		flex-shrink: 0;
		width: 50%;
		padding: 0 1rem 1.6rem 0;
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}
