@import "ScssHelpers/helpers-all.scss";

.shipments_included {
	@include bp($point_2, min) {
		.onboarding_2 & {
			display: none;
		}
	}

	@include bp($point_3) {
		.onboarding_2 & {
			height: 0;
			opacity: 0;
			transform: translateY(-2rem);

			transition: opacity 0.3s ease, transform 0.3s ease;
		}

		.onboarding_2--active_state & {
			height: 100%;
			opacity: 1;
			transform: translateY(0);
		}
	}
}

.shipments_included__title {
	font-weight: 500;
	line-height: 1;
	letter-spacing: -0.018em;

	@include bp($point_2, min) {
		font-size: 2.8rem;
		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}

	@include bp($point_3) {
		max-width: 26rem;
		margin: 0 auto;

		text-align: center;
		font-size: 1.8rem;
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}
